.b-have-question {
	letter-spacing: 0.01em;

	&__caption {
		font-size: 1.4rem;
		position: relative;
		color: $color-dark;
		font-weight: bold; }
	&__text {
		font-size: 1.2rem;
		line-height: 1.2;
		color: $color-gray;
		margin-bottom: 1.2rem; }
	&__icon {
		position: absolute;
		top: -1rem;
		right: 1.3rem;
		font-size: 2.4rem;
		color: $color-light-bg; } }
