.button {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	font-size: 1.1rem;
	font-weight: bold;
	text-align: center;
	letter-spacing: 0.03em;
	text-transform: uppercase;
	color: #ffffff;
	min-width: 16.8rem;
	min-height: 3.2rem;
	max-width: 100%;
	padding: .5rem 1.1rem;
	background-color: $color-blue;
	@include border-radius(2em);

	&:hover,
	&:focus {
		background-color: $color-blue-light; }
	.e-icon {
		color: inherit;
		margin-right: .8rem; }
	&:disabled,
	&[disabled] {
		background-color: $color-light; }
	&--cap {
		text-transform: capitalize; }
	&--white {
		background-color: #ffffff;
		border: 1px solid $color-blue;
		color: $color-blue;
		&:hover,
		&:focus {
			border-color: transparent;
			color: #ffffff; }
		&:disabled,
		&[disabled] {
			background-color: transparent;
			border-color: $color-light;
			color: $color-light; } }
	&--sm {
		font-weight: 500;
		min-width: auto; }
	&--md {
		font-weight: 500;
		min-width: 9.6rem;
		padding: .5rem 1.6rem; }
	&--lg {
		font-size: 1.3rem;
		min-width: 18.6rem;
		min-height: 4rem; } }

.button-link {
	color: $color-blue;
	font-weight: 500;

	&__icon {
		font-size: 1.2rem;
		margin-right: .5rem; } }
