.b-paymens-pie {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	&__chart {
		position: relative;
		padding-right: 2.4rem; }
	&__tooltip {}


	@media screen and (min-width: $lg) {
		justify-content: flex-end;
		&__chart {
			padding: 0 1.2rem; }
		&__tooltip {
			padding: 0 2.4rem 0 1.2rem; } } }
