.b-chart-labels {
	&__item {
		position: relative;
		margin-bottom: 1.1rem;
		padding-left: 1rem;
		&--all {
			margin-top: 3.5rem !important; } }
	&__lab-color {
		position: absolute;
		left: 0;
		top: 50%;
		bottom: 0;
		width: .4rem;
		height: 3.2rem;
		@include border-radius(2px);
		@include translate(0, -50%); }
	&__title {
		font-size: 1.2rem;
		letter-spacing: 0.01em;
		color: $color-gray-light;
		margin-bottom: .2rem; }
	&__wallet {
		font-weight: 500;
		color: $color-gray;
		span {
			font-weight: normal; } }
	&--style-2 {
		.b-chart-labels {
			&__lab-color {
				height: 4.8rem;
				@include border-radius(4px); }
			&__title {
				color: $color-gray;
				margin-bottom: 1.2rem;
				width: 100%; }
			&__wallet {
				width: 100%;
				font-size: 1.8rem;
				font-weight: bold;
				color: $color-dark; } } }
	@media screen and (min-width: $xs) {
		display: flex;
		flex-wrap: wrap;
		&__item {
			margin: 1.1rem;
			&--all {
				flex: 1 0 100%;
				max-width: 100%; } } }
	@media screen and (min-width: $lg) {
		&--style-2 {
			.b-chart-labels {
				&__wallet {
					font-size: 2.4rem; } } } }
	@media screen and (min-width: $xl) {
		flex-direction: column;
		&__item {
			margin: 0 0 1.1rem; } } }
