.b-statistic-chart {
	&__header {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		margin-bottom: 4.7rem;
		&--mb-md {
			margin-bottom: 3.2rem; } }
	&__header-item {
		//margin-right: 2.4rem
		padding-right: 2.4rem;
		flex: 1;
		&:last-child {
			width: 100%;
			max-width: 24rem;
			padding-right: 0;
 } }			//margin-left: auto

	&__body {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }
	&__chart-wrap {
		position: relative; }
	&__chart {
		//padding-right: 1.2rem
		position: relative;
		min-height: 30vh;
		width: 100%;
		margin-bottom: 2rem; }
	&__chart-canvas {
		max-width: 100%; }


	@media screen and (max-width: $max-sm) {

		&__header-item {
			flex: 1 0 100%;
			max-width: 100%;
			padding: 0;
			margin-bottom: 1rem;
			&:last-child {
				max-width: 100%; }
			&--second {
				order: 1; }
			&--first {
				order: 3; }

			&--last {
				order: 2; } } }



	@media screen and (min-width: $sm) {
		&__header-item {
			&--second {
				flex: 1 0 25%;
				max-width: 25%; }
			&--last {
				flex: 1 0 55%;
				max-width: 55%; } } }
	@media screen and (min-width: $lg) {
		&__header-item {
			&--second {
				max-width: 12rem; }
			&--last {
				max-width: 24rem; } } }

	@media screen and (min-width: $xl) {
		&__chart {
			padding-right: 1.2rem;
			width: calc(100% - 15rem);
			margin-bottom: 0; }
		&__chart-desc {
			padding: 0 2.4rem 0 1.2rem; } } }
