.b-paymens-pie-tooltip {
	&__item {
		position: relative;
		padding-left: 1.6rem;
		margin: 1.4rem 0; }
	&__lab-color {
		position: absolute;
		top: .25rem;
		left: 0;
		width: .8rem;
		height: .8rem;
		@include border-radius(50%); }
	&__title {
		font-size: 1rem;
		letter-spacing: 0.03em;
		color: $color-gray; }
	&__val {
		font-weight: bold;
		letter-spacing: 0.03em; } }
