.b-currency {
	padding: .5rem .8rem .5rem .8rem;
	background-color: #FFFFFF;
	border: 1px solid rgba($color-gray-light, .2);
	border-radius: 4px;
	//width: 7.6rem
	cursor: pointer;
	&.selected {
		background-color: $color-blue-light;
		color: #ffffff;
		.b-currency__name {
			color: inherit; } }
	&__name {
		font-size: 1.2rem;
		letter-spacing: 0.01em;
		font-weight: bold;
		color: $color-blue; }
	&__price {
		font-size: 1.1rem;
		font-weight: 500; } }

