.b-main-account-block {
	display: flex;
	flex-direction: column;
	flex: 1;
	&__balance {
		padding: 1.4rem 1.7rem;
		margin-bottom: 1.2rem;
		color: $color-gray;
		border-radius: 8px;
		background-color: rgba($color-gray-light, .1);
		&--blue {
			color: #ffffff;
			box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.05);
			background: linear-gradient(180deg, #2A73FF 0%, #0D57E7 100%);
			.b-main-account-block__walet {
				color: inherit; } } }

	&__title {
		font-weight: bold;
		font-size: 10px;
		letter-spacing: 0.04em;
		text-transform: uppercase; }

	&__title {
		display: block;
		margin-bottom: .7rem; }
	&__walet {
		font-size: 1.8rem;
		font-weight: bold;
		color: $color-dark;
		span {
			font-size: .8em;
			font-weight: normal; } }
	&__currency {
		display: flex;
		flex-direction: column;
		flex: 1;
		margin-top: 1rem; } }
