.b-sub-category {
	&__item {
		position: relative;
		display: block;
		width: 100%;
		color: $color-gray;

		padding: .45rem 1rem .45rem 9.3rem;
		&:after {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			width: .4rem;
			height: 100%;
			background-color: $color-blue;
			display: none; }
		&.active {
			&:after {
				display: block; } }
		&.active,
		&:hover,
		&:focus {
			color: $color-blue; } } }
