body,
html {
  height: 100%;
  margin: 0;
}

html {
  font-size: 62.5%;
}

body {
  font: 1.4rem/1.4 "Helvetica", Helvetica, Arial, sans-serif;
  color: #444444;
}

body * {
  box-sizing: border-box;
  outline: none;
}

body *:hover, body *:focus {
  outline: none;
}

body.modal-open {
  overflow: hidden;
}

a {
  text-decoration: none;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

img {
  max-width: 100%;
  vertical-align: middle;
}

h1, h2, h3, h4, h5, h6 {
  letter-spacing: 0.01em;
  margin-top: 0;
  margin-bottom: 1.5rem;
}

h5, .h5 {
  font-size: 1.8rem;
}

p {
  font-size: 1.4rem;
  margin-top: 0;
  margin-bottom: 1.5rem;
}

button {
  cursor: pointer;
}

button, input, textarea, .e-input {
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

input, textarea, .e-input {
  display: block;
  border: 2px solid transparent;
  padding: 13px 11px 11px;
  width: 100%;
  font-size: 16px;
}

input:focus, input:required:valid, textarea:focus, textarea:required:valid, .e-input:focus, .e-input:required:valid {
  border-color: #256EFB;
}

@media screen and (min-width: 1025px) {
  input, textarea, .e-input {
    padding: 15px 11px 13px;
  }
}

input[type="time"]::-webkit-outer-spin-button,
input[type="time"]::-webkit-inner-spin-button,
input[type="time"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  -moz-appearance: textfield;
  margin: 0;
  padding: 0;
  width: 0;
  display: none;
}

input[type="time"] {
  font-size: 1.3rem;
  padding-right: 0;
}

button {
  padding: 0;
  background-color: transparent;
  border: none;
  box-shadow: none;
}

video {
  max-width: 100%;
}

table {
  width: 100%;
}

::selection {
  background-color: #256EFB;
