.burger {
  position: relative;
  width: 32px;
  height: 32px;
  transition-duration: 0.5s;
  z-index: 101;
}

.burger:hover {
  cursor: pointer;
}

.burger__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 3px;
  width: 21px;
  transition-duration: 0.5s;
  background-color: #256EFB;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.burger__icon:before {
  transition-duration: 0.5s;
  position: absolute;
  top: -6px;
  left: 0;
  width: inherit;
  height: inherit;
  background-color: inherit;
  content: "";
}

.burger__icon:after {
  transition-duration: 0.5s;
  position: absolute;
  top: 6px;
  left: 0;
  width: .8em;
  height: inherit;
  background-color: inherit;
  content: "";
}

.burger.active .burger__icon {
  transition-duration: 0.5s;
  background: transparent;
}

.burger.active .burger__icon:before {
  -webkit-transform: rotateZ(45deg) scaleX(1.25) translate(4px, 4px);
  transform: rotateZ(45deg) scaleX(1.25) translate(4px, 4px);
  background-color: #256EFB;
}

.burger.active .burger__icon:after {
  -webkit-transform: rotateZ(-45deg) scaleX(1.25) translate(4px, -4px);
  transform: rotateZ(-45deg) scaleX(1.25) translate(4px, -4px);
  background-color: #256EFB;
  width: inherit;
}

@media screen and (min-width: 1025px) {
