.b-inform-block {
	position: relative;
	display: flex;
	&__caption {
		margin-bottom: .4rem; }
	&__text {
		color: $color-light-text;
		letter-spacing: 0.02em;
		margin-bottom: 1.1rem;
		line-height: 1.3; }
	&__picture {
		padding: 0 1.8rem 0 0; }
	&__body {
		display: flex;
		flex-direction: column; }
	&__bottom {
		margin: auto 0 0 auto; }
	&__link {
		font-size: 1rem;
		color: $color-blue;
		letter-spacing: 0.02em;
		font-weight: bold;
		text-transform: uppercase; }
	&__link-icon {
		font-size: 1.3rem;
		margin-left: .8rem;
		vertical-align: middle; }
	&__icon {
		font-size: 2.4rem;
		&--pink {
			color: $color-pink; }
		&--blue {
			color: $color-blue-light; } }

	//@media screen and (min-width: $lg)
