.b-navigation {
	margin-bottom: 3.3rem;
	&__item {
		position: relative;
		font-size: 1.4rem;
		letter-spacing: 0.02em;
		font-weight: 500;

		&.open {
			.b-navigation__sub-category {
				display: block; }
			.b-navigation__item-link {
				&.active {
					color: $color-gray;
					&:after {
						display: none; } } } } }

	&__item-icon {
		font-size: 15px;
		margin-right: 1.3rem; }

	&__item-link {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		color: $color-gray;
		padding: .75rem 1rem .75rem 6.4rem;
		&:after {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			width: .4rem;
			height: 100%;
			background-color: $color-blue;
			display: none; }
		&.active {
			color: $color-blue;
			&:after {
				display: block; } }
		&:hover,
		&:focus {
			color: $color-blue !important; } }
	&__sub-category {
		display: none;
		margin: .5rem 0;
		@include transition(all, .6s, linea); } }
