.b-select-currency {
	position: relative;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	&__body {
		position: relative;
		z-index: 1;
		display: flex;
		flex-direction: column;
		min-height: 100%; }

	&__caption {
		font-size: 12px;
		letter-spacing: 0.02em;
		font-weight: 500;
		color: $color-gray;
		margin-bottom: 1rem; }

	&__list,
	&__list-hidden {
		display: flex;
		flex-wrap: wrap;
		margin: -.4rem; }
	&__list-hidden {
		position: absolute;
		top: -.4rem;
		left: -2.4rem;
		right: -2.4rem;
		min-height: 100%;
		padding: 0 2rem 2rem;
		opacity: 0;
		visibility: hidden;
		box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.06);
		border-radius: .8em;
		background-color: #ffffff;
		z-index: 5;
		margin: 0;
		@include transition(all, .4s, ease); }
	&__list-item {
		padding: .4rem;
		flex: 1 0 50%;
		max-width: 50%;
		@include transition(all, .4s, ease);
		&.disabled {
			display: none; } }
	&__button {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 3rem;
		height: 3rem;
		margin: auto auto 2.5rem;
		padding-top: 1.2rem;
		cursor: pointer;
		width: 100%;
		&--gutter-b-0 {
			margin-bottom: 0; } }
	&__button-icon {
		position: absolute;
		right: 0;
		bottom: 0;
		left: 0;
		font-size: 1.7rem;
		color: $color-blue;
		margin: auto;
		z-index: -1;
		@include transition(all, .4s, ease); }
	&.active {
		.b-select-currency {
			&__button-icon {
				@include rotate(180deg); }
			&__list-hidden {
				opacity: 1;
				visibility: visible; } } }
	@media screen and (min-width: $sm) {
		&__list-item {
			flex: 1 0 auto;
			max-width: 100%; } }
	@media screen and (min-width: $xl) {
		&__list-item {
			flex: 1 0 50%;
			max-width: 50%; } } }
