.b-status {
	display: flex;
	align-items: center;
	justify-content: center;
	min-width: 10.4rem;
	min-height: 3.2rem;
	letter-spacing: 0.01em;
	@include border-radius(2px);

	&--done {
		color: $color-green;
		background-color: rgba($color-green, .1); }
	&--declined {
		color: $color-declined;
		background-color: rgba($color-declined, .1); }
	&--in-progress {
		color: $color-blue;
		background-color: rgba($color-blue, .1); }
	&--sent {
		color: $color-orange;
		background-color: rgba($color-orange, .1); } }
