.b-finance-list {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	&__item {
		position: relative;
		flex: 1 0 100%;
		max-width: 100%;
		padding: 0 .8em;
		margin-bottom: 1.5rem; }


	@media screen and (min-width: $xs) {
		&__item {
			flex-basis: 50%;
			max-width: 50%; } }
	@media screen and (min-width: $sm) {
		&__item {
			flex-basis: 33.333%;
			max-width: 33.333%; } }

	@media screen and (min-width: $sm1) {
		&__item {
			flex-basis: auto;
			max-width: 100%;
			padding: 0 1.3em; } }

	@media screen and (min-width: 1260px) {
		&__item {
			padding: 0 2em 0 2rem; } } }
