.col-tile {
	display: flex;
	padding: 1.2rem 0;
	min-height: 100%;
	&__header {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 2rem;
		.col-tile__caption {
			margin: 1rem 0; }
		&--block {
			display: block; } }
	&__content {
		display: flex;
		flex-direction: column;
		background-color: #ffffff;
		padding: 2.2rem 2.4rem 2rem;
		min-height: 100%;
		min-width: 100%;
		box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.06);
		@include border-radius(.8em);
		&--no-gutter {
			padding-left: 0;
			padding-right: 0;
			.col-tile__header {
				padding: 0  2.4rem; } } }
	&__caption {
		font-size: 1.8rem;
		letter-spacing: 0.02em;

		color: $color-dark;
		font-weight: bold;
		margin-bottom: 3rem;
		&--sm {
			text-transform: uppercase;
			font-size: 1rem;
			margin-bottom: 2.1rem; } }
	@media screen and (min-width: $lg) {
		&__caption {
			font-size: 2.4rem;
			&--sm {
				font-size: 1rem; } } } }
