
//Color
$color-accent: #256EFB;
$color-font: #47443d;
$color-light-text: #696969;
$color-dark: #444444;
$color-dark-light: #696969;
$color-gray: #888888;
$color-gray-light: #C3C3C3;
$color-light: #E1E1E1;
$color-blue: #256EFB;
$color-blue-light: #6E9FFF;
$color-light-bg: #F0F3F8;
$color-pink: #FF3C6B;
$color-declined: #FF647C;
$color-green: #00C48C;
$color-orange: #FDB900;


//Font
$font-default: 'Helvetica', Helvetica, Arial, sans-serif;


//Media queries
$xs: 481px;
$sm: 576px;
$sm1: 768px;
$md: 993px;
$lg: 1025px;
$xl: 1201px;
$xxl: 1601px;
$xxxl: 1801px;


$max-xs: $xs - 1px;
$max-sm: $sm - 1px;
$max-sml: $sm1 - 1px;
$max-lg: $lg - 1px;
$max-xl: $xl - 1px;
$max-xxl: $xxl - 1px;
$max-xxxl: $xxxl - 1px;

// Customization Bootstrap vars from "app/libs/bootstrap/scss/_variables.scss"
$grid-gutter-width: 30px;
$grid-columns: 12;
$grid-breakpoints: (xs: 0, sm: 576px, md: 768px, lg: 1025px, xl: 1201px);
$container-max-widths: (sm: 540px, md: 730px, lg: 990px, xl: 1200px);

$gutter: $grid-gutter-width;
