.b-table {
	border-collapse: collapse;
	&__header {
		font-weight: bold;
		letter-spacing: 0.02em;
		border-bottom: 1px solid rgba($color-blue-light, .5); }
	&__header-item {
		padding: 1.8rem 1rem 2rem 1rem;
		text-align: left;
		&:first-child {
			padding-left: 1.4rem; }
		&:nth-child(3) {
			text-align: right; } }
	&__row {
		border: 1px solid transparent;
		border-bottom: 1px solid rgba($color-blue-light, .5); }

	&__col {
		padding: 2rem 1rem;
		color: $color-dark-light;
		&:first-child {
			padding-left: 1.4rem; }
		//&:nth-child(2)
		//	text-align: center
		&:nth-child(3) {
			color: $color-dark;
			text-align: right; }
		&:nth-child(4) {
			color: $color-gray; }
		&:last-child {
			text-align: center; } }
	&__time {
		color: $color-gray-light; }
	@media (min-width: $lg) and (max-width: 1100px) {
		display: inline-block;
		max-width: 100%;
		overflow-x: auto; }
	@media screen and (max-width: $max-sml) {
		display: inline-block;
		max-width: 100%;
		overflow-x: auto; }
	@media screen and (min-width: $xl) {
		&__header-item {
			padding: 1.8rem 1.6rem 2rem 1.6rem;
			&:first-child {
				padding-left: 2.4rem; } }
		&__col {
			padding: 2.7rem 1.6rem;
			&:first-child {
				padding-left: 2.4rem; } } } }
