.b-user-account {
	position: relative;
	display: flex;

	padding-left: 1.5rem;
	border-left: 1px solid rgba(195, 195, 195, .2);

	&__avatar {
		overflow: hidden;
		@include border-radius(50%);
		margin-right: 1rem; }
	&__inform {
		font-size: 1.4rem;
		letter-spacing: 0.01em;
		color: $color-gray;
		padding: 0 3.4rem 0 1.8rem;
		display: none; }
	&__number {
		font-size: 1.2rem;
		line-height: 1;
		margin-bottom: .7rem; }
	&__alias {
		font-weight: bold;
		line-height: 1.2; }
	&__arrow {
		align-self: center;
		cursor: pointer;
		line-height: 0; }
	&__arrow-icon {
		font-size: 1.6rem;
		color: $color-blue; }

	@media screen and (min-width: $sm) {
		&__inform {
			display: block; }
		&__avatar {
			margin-right: 0; } }

	@media screen and (min-width: $lg) {
		padding-left: 2rem; } }
