.b-period-chart {
	font-size: 1.2rem;
	color: $color-gray;
	margin: 2rem 0 1rem;
	&__caption {
		font-weight: 500;
		letter-spacing: 0.02em; }
	&__row {
		display: flex;
		flex-wrap: wrap;
		align-items: center; }
	&__item {
		font-size: 1.2rem;
		font-weight: bold;
		cursor: pointer;
		margin-right: 1.6rem;
		&:last-child {
			margin-right: 0; }
		&.active,
		&:hover,
		&:focus {
			color: $color-blue; } }

	@media screen and (min-width: $sm) {
		margin: 0; } }
