.c-page {
	display: flex;
	flex-direction: column;
	min-height: 100%;
	background-color: $color-light-bg; }

.c-main {
	//flex: 1 0 auto
	//display: flex
	//height: 100%
	main {
		position: relative;
		flex: 1;
		padding: 7.5rem 0 .75rem; } }

.c-header {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 11;
	padding: 1.2rem 0 .6rem;
	border: 1px solid rgba(195, 195, 195, 0.2);
	background-color: #ffffff;
	&__container {
		margin: 0 auto;
		padding: 0 1.5rem; }

	&__logo {
		max-width: 100%;
		flex: 0 0 auto;
		padding-left: 15px;
		padding-right: 15px; }
	&__account-type {
		display: none;
		max-width: 10rem;
		flex: 1 0 auto;
		padding-left: 15px;
		padding-right: 15px;
		.b-link-icon {
			line-height: .8; } }
	&__user-account {
		max-width: 100%;
		flex: 1 0 auto;
		padding-left: 15px;
		padding-right: 15px;
		display: flex;
		align-items: center;
		justify-content: flex-end; }
	&__user-account-notification {
		padding-right: .9rem; }
	&__burger {
		display: flex;
		align-items: center;

		padding-left: 15px;
		padding-right: 15px; } }
//.c-content
//	padding: 50px 0 50px
.c-col {
	padding-left: 1.5rem;
	padding-right: 1.5rem; }

.c-footer {
	padding: 30px 0px;
	flex: 0 0 auto; }
.container-main {
	padding-left: 1.5rem;
	padding-right: 1.5rem; }
.main-link {
	color: $color-blue; }
.in-progress {
	color: $color-green; }
.declined {
	color: $color-declined; }
.currency {
	font-weight: normal;
	color: $color-gray; }
@media screen and (max-width: $max-sml) {
	.mt-md-1 {
		margin-top: 1rem; } }
@media screen and (max-width: $max-lg) {
	.c-page {
		&.js-modal-open {
			overflow: hidden;
			main {
				&:after {
					content: '';
					position: fixed;
					top: 0;
					left: 0;
					height: 100%;
					width: 100%;
					background-color: rgba(0,0,0, .5);
					z-index: 5; } } } }
	.c-header {
		//position: fixed
		//top: 0
		//right: 0
 } }		//left: 0
@media screen and (min-width: $sm) {
	.c-header {
		&__account-type {
			display: block; } } }


@media screen and (min-width: $lg) {
	.c-main {
		padding-left: 25.6rem;
		main {
			padding: 7.5rem 0 1.2rem; } }
	.container-main {
		padding-left: 3.2rem;
		padding-right: 6.4rem; }
	.c-header {
		&__container {
			padding: 0 6.4rem; }
		&__logo {
			max-width: 19%;
			flex-basis: 19%; }
		&__user-account {
			max-width: 100%;
			flex-basis: auto; }
		&__account-type {
			max-width: 100%; }
		&__burger {
			display: none; } } }
