.b-invoice-desc {
	&__header-item {
		padding: 0 1rem 0 1rem;
		color: $color-gray-light;
		letter-spacing: 0.01em;
		text-align: left;
		font-weight: 500;
		&:first-child {
			padding-left: 1.4rem; } }
	&__col {
		padding: 0 1rem;
		font-weight: 500;
		color: $color-light-text; }

	@media screen and (min-width: $xl) {
		&__header-item {
			padding: 0 1.6rem 0 1.6rem;
			&:first-child {
				padding-left: 2.4rem; } }
		&__col {
			padding: 0rem 1.6rem;
			&:first-child {
				padding-left: 2.4rem; } } } }
