.b-pagination {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;

	&__body {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		background: #ffffff;
		box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.06);
		padding: 0 2.4rem;
		@include border-radius(2em); }
	&__page {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-shrink: 0;
		min-width: 3.6rem;
		height: 4rem;
		margin: 0;
		text-transform: uppercase;
		font-size: 1.4rem;
		font-weight: bold;
		letter-spacing: 0.03em;
		color: $color-blue;
		&.active {
			background-color: $color-blue;
			color: #ffffff;
			&:hover,
			&:focus {
				color: #ffffff;
				opacity: .95; } }
		&:focus,
		&:hover {
			color: $color-blue-light; }
		&--stub {
			border-color: transparent; }
		&--prev {
			font-size: 1.1rem;
			width: auto;
			margin-right: 1.5rem;
			.b-pagination__page-icon {
				margin-right: 1rem;
				@include rotate(90deg); } }
		&--next {
			font-size: 1.1rem;
			width: auto;
			margin-left: 1.5rem;
			.b-pagination__page-icon {
				margin-left: 1rem;
				@include rotate(-90deg); } }
		&--disabled {
			color: $color-blue-light; } }
	&__page-icon {
		font-size: 1.1rem; }

	@media screen and (min-width: $lg) {
		&__page {
			&--prev {
				margin-right: 4rem; }
			&--next {
				margin-left: 4rem; } } } }
