.b-sidebar {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;

	min-height: 100%;
	padding: 8.3rem 0 2.5rem;

	background-color: #ffffff;
	box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.06);
	width: 25.6rem;
	z-index: 10;
	overflow-y: auto;
	@include transition(all, .6s, ease);
	&__body {
		display: flex;
		flex-direction: column;
		flex: 1;
		height: 100%; }
	&__bottom {
		margin-top: auto;
		margin-bottom: 2.5rem;
		padding: 0rem 1.5rem 0 6.4rem; }
	@media screen and (min-width: $lg) {
		&__bottom {
			//position: sticky
 } }			//bottom: 7rem
	@media screen and (max-width: $max-lg) {
		//position: fixed
		left: -256px;
		//top: 0
		//bottom: 0
		//padding-top: 8.9rem
		overflow: auto;
		&.active {
			left: 0; } } }
