.b-chart-tooltip {
	font-size: 1.2rem;
	position: absolute;
	left: 50%;
	z-index: 10;
	top: 35%;
	transform: translate(-50%, -50%);
	opacity: 0;
	&__body {
		position: relative;
		background-color: rgba($color-gray, .9);
		color: #ffffff;
		padding: 1rem 1.4rem;
		min-width: 19rem;
		@include border-radius(.6em);
		&:before {
			content: '';
			position: absolute;
			top: calc(100% - 1px);
			left: 0;
			right: 0;
			margin: 0 auto;
			width: 0;
			height: 0;
			border-left: .75rem solid transparent;
			border-right: .75rem solid transparent;
			border-top: .8rem solid rgba($color-gray, .9); } }
	&__header {
		font-size: 1rem;
		text-align: left;
		letter-spacing: 0.066px;
		text-transform: uppercase;
		font-weight: 500;
		th {
			padding-bottom: .6rem; } }
	&__item {
		font-size: 1.2rem;
		letter-spacing: 0.01em;
		line-height: 1; }
	&__circle {
		display: inline-block;
		width: .8rem;
		height: .8rem;
		margin-right: .7rem;
		@include border-radius(50%); }
	&__val {
		text-align: right; }
	&__payments {
		td {
			padding-top: .7rem; } } }
