.b-select {
	position: relative;
	font-size: 1.4rem;
	width: 100%;
	&__header {
		position: relative;
		z-index: 1;
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 1rem 1.6rem;
		border: 1px solid $color-light;
		color: $color-dark;
		background-color: #ffffff;
		@include border-radius(6px); }
	&__header-val {
		//text-transform: uppercase
		overflow: hidden;
		white-space: nowrap;
		color: $color-gray;
		&.selected {
			color: $color-dark; } }
	&__header-placeholder {
		color: $color-gray; }
	&__header-icon {
		position: absolute;
		right: 1rem;
		font-size: 1.6rem;
		color: $color-gray-light;
		flex-shrink: 0;
		@include transition(all, .4s, ease); }
	&__dropdown {
		position: absolute;
		top: calc(100% + .5rem);
		left: 0;
		right: 0;
		background-color: #ffffff;
		box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.14);
		@include border-radius(6px);
		@include transition(all, .4s, ease);
		z-index: 5;
		opacity: 0;
		visibility: hidden;
		overflow: hidden; }
	&__item {
		display: block;
		padding: 1.1rem 1.6rem;
		cursor: pointer;
		color: $color-gray;
		&.active,
		&:hover,
		&:focus {
			background-color: $color-blue-light;
			color: #ffffff; } }
	&.active {
		.b-select {
			&__header {
				box-shadow: 0px 1px 3px transparent;
				border: 1px solid $color-blue-light; }
			&__dropdown {
				opacity: 1;
				visibility: visible; }
			&__header-icon {
				@include rotate(180deg); } } } }

