.b-options {
	position: relative;
	&__button {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 2.2rem;
		height: 2.2rem;
		color: $color-gray-light; }

	&__drobdown {
		position: absolute;
		top: calc(100% + 1rem);
		right: 0;
		z-index: 5;
		background-color: #FFFFFF;
		padding: 1rem 0 0;
		box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.1);
		border-radius: .5em;
		overflow: hidden;
		opacity: 0;
		visibility: hidden;
		max-height: 0;
		@include transition(all, .45s, linear); }
	&__drobdown-item {
		display: flex;
		align-items: center;

		padding: .9rem 1.6rem;
		font-weight: 500;
		color: $color-gray;
		letter-spacing: 0.004em;
		min-width: 16.8rem;
		.e-icon {
			margin-right: .6rem; }
		&.active,
		&:focus,
		&:hover {
			color: #ffffff;
			background-color: $color-blue-light; }

		&--delete {
			margin-top: .8rem;
			padding: 1.6rem;
			border-top: 1px solid rgba(#E1E1E1, .3); } }

	&.active {
		.b-options__drobdown {
			opacity: 1;
			visibility: visible;
			max-height: initial; } } }
