body,
html {
	height: 100%;
	margin: 0; }
html {
	font-size: 62.5%; }
body {
	font: 1.4rem/1.4 $font-default;
	color: $color-dark;

	* {
		box-sizing: border-box;
		outline: none;
		&:hover,
		&:focus {
			outline: none; } }

	&.modal-open {
		overflow: hidden; } }
a {
	text-decoration: none;
	//color: $dark
	@include transition(all, .4s, ease); }


img {
	max-width: 100%;
	vertical-align: middle; }
h1, h2, h3, h4, h5, h6 {
	letter-spacing: 0.01em;
	margin-top: 0;
	margin-bottom: 1.5rem; }

h5, .h5 {
	font-size: 1.8rem; }
p {
	font-size: 1.4rem;
	margin-top: 0;
	margin-bottom: 1.5rem; }
button {
	cursor: pointer; }
button, input, textarea, .e-input {
	@include transition(all, .4s, ease); }


input, textarea, .e-input {
	display: block;
	border: 2px solid transparent;
	//background-color: $grey-light
	padding: 13px 11px 11px;
	width: 100%;
	font-size: 16px;
	&.error, &:focus:required:invalid {
 }		//border-color: $input-error-border
	&:focus,
	&:required:valid {
		border-color: $color-accent; }
	@media screen and (min-width: $lg) {
		padding: 15px 11px 13px; } }

input[type="time"]::-webkit-outer-spin-button,
input[type="time"]::-webkit-inner-spin-button,
input[type="time"]::-webkit-inner-spin-button {

	-webkit-appearance: none;
	-moz-appearance: textfield;
	margin: 0;
	padding: 0;
	width: 0;
	display: none; }

input[type="time"] {
	font-size: 1.3rem;
	padding-right: 0; }
button {
	padding: 0;
	background-color: transparent;
	border: none;
	box-shadow: none; }
video {
	max-width: 100%; }
table {
	width: 100%; }

::selection {
	background-color: $color-accent;
	color: #fff;
	text-shadow: none; }

::placeholder {
	font-size: 1.4rem; }


ul {
	list-style: none;
	padding: 0;
	margin: 0; }
