.b-notification {
	position: relative;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	min-height: 3.2rem;
	min-width: 3.2rem;
	&__icon {
		position: relative;
		display: inline-block;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 12px;
			height: 7px;
			margin: auto;
			box-shadow: 0px 3px 6px rgba(37, 110, 251, 0.3), 0px 0px 2px rgba(0, 0, 0, 0.08); }
		svg {
			font-size: 1.9rem;
			color: $color-blue; } } }
